import opg from 'ts/opg.ts';
import PopUp from 'ts/ui/Popup';
import {Languages} from 'ts/util/Languages';
import {Combo} from 'ts/ui/Combo';
import {url} from "ts/util/utils";
import {Cache} from 'ts/util/store';


const lpg = Languages.package;
console.log('Languages:', Languages);

const cache = Cache.getInstance();
const row = cache.get('row');
console.log(row);


const subjectPage = __uri('/pages/content/media/addSubject.html');


opg.api({
	'addMain!post': 'mediaexhibtion/AddMediaExhibitonInfoSubject',
	'updateMain!post': 'mediaexhibtion/UpMediaExhibitonInfoSubject',
	getSubjects: 'mediaexhibtion/MediaExhibitionInfoById',
	delSubject: 'mediaexhibtion/DelMediaExhibitionCoontentById',
	'upload!UPLOAD': 'upload/files',
});




const categoryId = ~~opg.request['categoryId'];
const categoryData = cache.get('categoryData', []);
console.log(categoryData);
let selCategory = opg('#mediaexhibitiontype_id').listBox({
	data: categoryData,
	autoPrependBlank: false,
});



let informationId;

jQuery.datetimepicker.setLocale(Languages.current);
$('#publishbegindate,#publishenddate').each(function (i, elem) {
	$(elem).datetimepicker({
		//timepicker: false,
		//closeOnDateSelect: true,
		format: 'Y-m-d H:i:00',
		step: 10
	});
});


let iptCoverPicture = $('#coverpicture');
let tdCover = $('#tdCover');
Combo.makeClearableInput(iptCoverPicture, $({}));
$('.ipt-eraser', '#tdUpload').on('click', function () {
	//if(!this.value){
	tdCover.empty();
	//}
});

let uploadForm = $(`<form>
						<input name="file" type="file" accept="image/jpg,image/jpeg,image/png">
				</form>`);
$('#btnUploadDialog').click(function () {
	let pop: PopUp = top.opg.confirm(uploadForm, function () {
		let formData = new FormData(uploadForm[0] as HTMLFormElement);
		opg.api.upload(formData, function (data) {
			iptCoverPicture.val(data[0].downloadLink);
			tdCover.html(`<img src="${data[0].downloadLink}" style="max-height: 100px;"/>`);
			pop.close();
		});
		return true;
	}, {
		title: `${lpg.upload}`,
		width: 200,
		height: 50,
		buttons: {
			ok: `${lpg.upload}`,
			cancel: `${lpg.cancel}`,
		},
	});


});


let ulSubjects = $('#ulSubjects');
let tbMain = $('#tbMain');
let btnAddSubject = $('#btnAddSubject');
if (row) {
	informationId = row.id;
	tbMain.jsonToFields(row);
	btnAddSubject.show();

	listSubjects();
}
else {
	categoryData.forEach((v, i) => {
		if (v.id === categoryId) {
			selCategory.selectedIndex = i;
		}
	});
}


function listSubjects(){
	opg.api.getSubjects({informationId}, (data) => {
		ulSubjects.bindList({
			list: data ,
			template: '<li><button class="btn-mini btn-info btnEditSub" data-id="${id}">'+lpg.edit+'</button> <button class="btn-mini btn-danger btnDelSub" data-id="${id}">'+lpg.del+'</button><hr>${content}</li>'
		})
	})
}

//edit subjects
ulSubjects.on('click' , '.btnEditSub' , function () {
	let btn = $(this) , subId = btn.data('id');
	let src = url.setParam(subjectPage , {id:informationId , subId});
	let pop: PopUp = top.opg.confirm(`<iframe src="${src}" />`, function (i, ifr) {
		ifr.doSave(pop, listSubjects);
		return true;
	}, {
		title: `${lpg.editSubject}`,
		width: 500,
		height: 450,
		buttons: {
			ok: `${lpg.save}`,
			cancel: `${lpg.cancel}`,
		},
	});
});



//delete subjects
ulSubjects.on('click' , '.btnDelSub' , function () {
	let btn = $(this) , subId = btn.data('id');
	opg.confirm(lpg.deleteConfirm , function () {
		opg.api.delSubject({id: subId} , ()=>{
			listSubjects();
		});
	})
});




$('#btnSaveMain').click(function () {
	let param = tbMain.fieldsToJson({
		title: {
			name: Languages.package.title,
			type: 'ns',
			require: true,
		},
	});

	if (param) {
		param.mediaExhibtionInfoTypeId = ~~categoryId;
		param.informationType = 1;
		param.enableStick = param.enableStick ? 1 : 0;

		//edit
		if (informationId) {
			param.id = informationId;
			param.stick = param.enableStick;

			opg.api.updateMain(param, function (data) {
				btnAddSubject.show();
			});
		}
		//add
		else {
			opg.api.addMain(param, function (data) {
				informationId = data;
				//$('input,button', tbMain).prop('disabled', true);
				btnAddSubject.show();
			});
		}


	}
});



btnAddSubject.click(function () {
	let src = url.setParam(subjectPage , {id:informationId});
	let pop: PopUp = top.opg.confirm(`<iframe src="${src}" />`, function (i, ifr) {
		ifr.doSave(pop, listSubjects);
		return true;
	}, {
		title: `${lpg.addSubject}`,
		width: 500,
		height: 450,
		buttons: {
			ok: `${lpg.save}`,
			cancel: `${lpg.cancel}`,
		},
	});
});